import React from 'react'
// import * as BooksAPI from './BooksAPI'
import {Route} from 'react-router-dom';
import BooksAppSample from './sample.js';
import BookSearch from './Component/BookSearch.js';
import MyReads from './Component/MyReads.js';
import * as BooksAPI from './BooksAPI';

class BooksApp extends React.Component{
  state = {
    searchResult:[],
    bookShelfs:{
      currentlyReading: [],
      wantToRead : [],
      read: []
    }
  }
  componentDidMount(){
    this.refreshShelf()
  }

  onSearchBook = (query)=>{
    BooksAPI.search(query).then(data=>{
      this.setState(currentState=>({searchResult:data}))
    })
  }
  updateShelf = (book,shelfname)=>{
    BooksAPI.update(book,{shelf:shelfname}).then(()=>{
      this.refreshShelf();
    })
  }
  updateShelfForSearch = (book,shelfname)=>{
    BooksAPI.update(book,{shelf:shelfname});
  }
  refreshShelf = ()=>{
    BooksAPI.getAll().then(data=>{
      let currentlyReading = data.filter(book=>book.shelf === 'currentlyReading');
      let wantToRead = data.filter(book=>book.shelf === 'wantToRead');
      let read = data.filter(book=>book.shelf === 'read');
      let bookShelfs = {
        currentlyReading: currentlyReading,
        wantToRead : wantToRead,
        read: read
      }
      this.setState(currentState=>({bookShelfs:bookShelfs}));
    });
  }

  render(){
    return (
      <div>
        <Route path='/sample' component={BooksAppSample}/>
        <Route exact path ='/' render = {()=>(
          <MyReads
            bookShelfs = {this.state.bookShelfs}
            update = {this.updateShelf}
            refreshShelf={this.refreshShelf}/>
        )}/>
        <Route path ='/search' render ={()=>(
          <BookSearch
            searchResult = {this.state.searchResult}
            onSearchBook={this.onSearchBook}
            update = {this.updateShelfForSearch}/>
        )}/>
      </div>
    )
  }
}

export default BooksApp;



