import React from 'react';
import SearchBar from './SearchBar.js';
import BookList from './BookList.js';

class BookSearch extends React.Component{
  constructor(props){
    super(props);
    this.onSearchBook = this.onSearchBook.bind(this);
  }
  onSearchBook = (query)=>{
    if(this.props.onSearchBook){
      this.props.onSearchBook(query)
    }
  }

  render(){
    const bookList = this.props.searchResult;
    return (
      <div className="app">
          <div className="search-books">
            <SearchBar onSearch = {this.onSearchBook}/>
          </div>
          <div className="search-books-results">
           {(bookList && bookList.length) &&
             <BookList bookList = {bookList} update = {this.props.update}/>
           }
          </div>
      </div>
    );
  }
}
export default BookSearch;



